<template>
  <v-hover v-slot:default="{ hover }">
    <v-card
      class="mx-auto"
      color="grey lighten-4"
      max-width="600"
      :href="link == null ? null : link"
    >
      <v-img
        :aspect-ratio="9/9"
        :src="imgUrl"
      >
        <v-expand-transition>
          <div
            v-if="hover"
            class="d-flex transition-fast-in-fast-out grey darken-4 v-card--reveal display-3 white--text"
            style="height: 100%;"
          >
            <h5 style="padding:10px;" >{{itemTitle}}</h5>
            <p class = "overline" style="padding:10px;">{{itemDescription}}</p>
          </div>
        </v-expand-transition>
      </v-img>
    
    </v-card>
  </v-hover>
</template>


<style>
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .99;
  position: absolute;
  width: 100%;
  cursor: pointer;
}
</style>

<script>
export default {
    name: "PortfolioItem",
    props: ["imgUrl", "itemTitle", "itemDescription", "link"],

}
</script>