<template>
  <v-form
    ref="form"
    v-model="valid"
    lazy-validation
    browser-autocomplete="off"
    method="post"
    action="https://formspree.io/f/xnqzrylk"
  >
    <!--Name-->
    <v-text-field
      v-model="name"
      name="name"
      :rules="nameRules"
      label="Name"
      required
      autocomplete="false"
    ></v-text-field>
    <!--Email-->
    <v-text-field
      v-model="email"
      name="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>
    <!--Organization Type-->
    <v-select
      v-model="type"
      name="type"
      :items="items"
      :rules="[v => !!v || 'Item is required']"
      label="Who are you?"
      required
    ></v-select>
    <!--Company Name-->
    <v-scale-transition>
        <v-text-field v-if="type == 'Company'"
        name="company"
        v-model="company"
        label="Company Name"
        ></v-text-field>
    </v-scale-transition>

    <!--Project Descriptions-->
    <v-textarea
          name="description"
          v-model="description"
          label="Project Description/Questions"
          hint="Describe your project briefly..."
    ></v-textarea>
    <br><br>

    <v-btn class="mr-3" depressed color="primary" type="submit">Submit</v-btn>
    <v-btn color="error" class="mr-4" @click="reset">
      Reset Form
    </v-btn>

  </v-form>
</template>


<script>

  export default {
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
      ],
      company: '',
      email: '',
      description: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      type: '',
      items: [
        'Company',
        'Individual',
        'Other'
      ],
    }),

    methods: {
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },
  }
</script>